import React, {useEffect, useState} from 'react';
import {useSelector} from "react-redux";
import {use} from "i18next";

const DominoesMyScore = ({endRoundOutcome}) => {

    const user = useSelector(state => state.userInfoReducer.data)

    const [score, setScore] = useState(0)

    useEffect(() => {
        if(Object.keys(user).length && user.id === endRoundOutcome?.winner) {
            setScore(endRoundOutcome?.total_points)
        } else if (endRoundOutcome.length > 1) {
            endRoundOutcome?.map(item => {
                if(item.id === user.id) {
                    setScore(item.points)
                }
            })
        }
    }, [endRoundOutcome, user])

    return (
        <div className="domino__grid--item">
            <div className="game__bet">
                <span className="game__bet--value">
                    {score}
                </span>
            </div>
        </div>
    );
};

export default DominoesMyScore;